'use strict';

import classManipulation from '../../_js/classManipulation'

export default class layoutModal {

  constructor() {

    var showModal = document.getElementsByClassName('show-modal');
    var hideModal = document.getElementsByClassName('hide-modal');
    var body = document.getElementsByTagName('body')[0];

    for(var i = 0; i < showModal.length; i++) {
      showModal[i].onclick = function(e) {
        classManipulation.addClass(body, 'js-modal-visible');
        var id = this.getAttribute('data-modal');
        var target = document.getElementById(id);
        classManipulation.addClass(target, 'js-modal-visible');
      }
    }

    for(var i = 0; i < hideModal.length; i++) {
      hideModal[i].onclick = function(e) {
        classManipulation.removeClass(body, 'js-modal-visible');
        for(var i = 0; i < document.getElementsByClassName('js-modal-visible').length; i++) {
          if(document.getElementsByClassName('js-modal-visible')[i].getElementsByTagName("iframe")[0]){
            var iframe = document.getElementsByClassName('js-modal-visible')[i].getElementsByTagName("iframe")[0];
            iframe.src = iframe.src;
          }
          classManipulation.removeClass(document.getElementsByClassName('js-modal-visible')[i], 'js-modal-visible');
        }

      }
    }

  }
}
