'use strict';

import classManipulation from '../../_js/classManipulation'

export default class layoutBanner {

  constructor() {

    var hideBanner = document.getElementsByClassName('hide-banner');

    for(var i = 0; i < hideBanner.length; i++) {
      hideBanner[i].onclick = function(e) {
        var id = this.getAttribute('data-banner');
        var target = document.getElementById(id);
        classManipulation.addClass(target, 'js-banner-hide');
      }
    }

  }
}
