'use strict';

import classManipulation from '../../_js/classManipulation'

export default class layoutSidebar {

  constructor() {

    var showSidebarLeft = document.getElementsByClassName('show-sidebar-left');
    var hideSidebarLeft = document.getElementsByClassName('hide-sidebar-left');
    var showSidebarRight = document.getElementsByClassName('show-sidebar-right');
    var hideSidebarRight = document.getElementsByClassName('hide-sidebar-right');
    var body = document.getElementsByTagName('body')[0];

    for(var i = 0; i < showSidebarLeft.length; i++) {
      showSidebarLeft[i].onclick = function() {
        classManipulation.addClass(body, 'js-sidebar-left-visible');
      }
    }

    for(var i = 0; i < hideSidebarLeft.length; i++) {
      hideSidebarLeft[i].onclick = function() {
        classManipulation.removeClass(body, 'js-sidebar-left-visible');
      }
    }

    for(var i = 0; i < showSidebarRight.length; i++) {
      showSidebarRight[i].onclick = function() {
        console.log('showSidebarRight')
        classManipulation.addClass(body, 'js-sidebar-right-visible');
      }
    }

    for(var i = 0; i < hideSidebarRight.length; i++) {
      hideSidebarRight[i].onclick = function() {
        classManipulation.removeClass(body, 'js-sidebar-right-visible');
      }
    }

  }
}
